<template>
  <div class="reconciliation-container">
    <el-card>
      <div slot="header" class="card-header">
        <span>对账管理</span>
        <el-button type="primary" @click="handleAdd">新增对账</el-button>
      </div>

      <!-- 搜索条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="账户">
          <el-input v-model="searchForm.accountId" placeholder="请输入账户"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="resetSearch">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 数据表格 -->
      <el-table
        v-loading="loading"
        :data="reconciliationList"
        style="width: 100%"
      >
        <el-table-column prop="accountId" label="账户"></el-table-column>
        <el-table-column prop="reconciliationDate" label="对账日期"></el-table-column>
        <el-table-column prop="startBalance" label="期初余额">
          <template slot-scope="scope">
            {{ formatMoney(scope.row.startBalance) }}
          </template>
        </el-table-column>
        <el-table-column prop="endBalance" label="期末余额">
          <template slot-scope="scope">
            {{ formatMoney(scope.row.endBalance) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ getStatusText(scope.row.status) }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next, jumper, total"
          :total="pageInfo.total"
          :current-page="pageInfo.pageNum"
          :page-size="pageInfo.pageSize"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
        ></el-pagination>
      </div>

      <!-- 编辑对话框 -->
      <el-dialog
        title="对账信息"
        :visible.sync="dialogVisible"
        width="500px"
        :close-on-click-modal="false"
      >
        <el-form :model="formData" :rules="rules" ref="formData" label-width="120px">
          <el-form-item label="账户" prop="accountId">
            <el-input v-model.number="formData.accountId" placeholder="请输入账户"></el-input>
          </el-form-item>
          <el-form-item label="对账日期" prop="reconciliationDate">
            <el-date-picker
              v-model="formData.reconciliationDate"
              type="date"
              placeholder="选择对账日期"
              value-format="yyyy-MM-dd"
              style="width: 100%"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="期初余额" prop="startBalance">
            <el-input v-model.number="formData.startBalance" placeholder="请输入期初余额"></el-input>
          </el-form-item>
          <el-form-item label="期末余额" prop="endBalance">
            <el-input v-model.number="formData.endBalance" placeholder="请输入期末余额"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select v-model.number="formData.status" placeholder="请选择状态">
              <el-option label="未对账" :value="0"></el-option>
              <el-option label="已对账" :value="1"></el-option>
              <el-option label="差异" :value="2"></el-option>
              <el-option label="已确认" :value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="handleSubmit">确定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { reconciliationGetList, reconciliationGetById, reconciliationSave, reconciliationDelete } from '@/api/reconciliationApi';

export default {
  name: "Reconciliation",
  data() {
    return {
      loading: false,
      searchForm: {
        accountId: '',
        reconciliationDate: '',
        status: ''
      },
      reconciliationList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      dialogVisible: false,
      formData: {
        id: '',
        accountId: '',
        reconciliationDate: '',
        startBalance: 0,
        endBalance: 0,
        status: 0
      },
      rules: {
        accountId: [
          { required: true, message: '请输入账户', trigger: 'blur' },
        ],
        reconciliationDate: [
          { required: true, message: '请选择对账日期', trigger: 'change' }
        ],
        startBalance: [
          { required: true, message: '请输入期初余额', trigger: 'blur' },
          { type: 'number', message: '期初余额必须是数字', trigger: 'blur' }
        ],
        endBalance: [
          { required: true, message: '请输入期末余额', trigger: 'blur' },
          { type: 'number', message: '期末余额必须是数字', trigger: 'blur' }
        ],
        status: [
          { required: true, message: '请选择状态', trigger: 'change' },
          { type: 'number', message: '状态必须是数字', trigger: 'blur' }
        ]
      }
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    // 获取列表数据
    async getList() {
      this.loading = true;
      try {
        const response = await reconciliationGetList({
          ...this.searchForm,
          pageNum: this.pageInfo.pageNum,
          pageSize: this.pageInfo.pageSize
        });
        const responseData = response.data || {};
        this.reconciliationList = responseData.records || [];
        this.pageInfo.total = responseData.total || 0;
      } catch (error) {
        this.$message.error('获取对账列表失败');
        console.error(error);
      } finally {
        this.loading = false;
      }
    },

    // 查询
    handleSearch() {
      this.pageInfo.pageNum = 1;
      this.getList();
    },

    // 重置查询条件
    resetSearch() {
      this.searchForm = {
        accountId: '',
        reconciliationDate: '',
        status: ''
      };
      this.getList();
    },

    // 分页变化
    handleCurrentChange(pageNum) {
      this.pageInfo.pageNum = pageNum;
      this.getList();
    },

    handleSizeChange(pageSize) {
      this.pageInfo.pageSize = pageSize;
      this.pageInfo.pageNum = 1;
      this.getList();
    },

    // 新增
    handleAdd() {
      this.formData = {
        id: '',
        accountId: '',
        reconciliationDate: '',
        startBalance: 0,
        endBalance: 0,
        status: 0
      };
      this.dialogVisible = true;
    },

    // 编辑
    async handleEdit(row) {
      this.loading = true;
      try {
          this.formData = {
          ...row
        };
        this.dialogVisible = true;
      } catch (error) {
        this.$message.error('获取对账信息失败');
        console.error(error);
      } finally {
        this.loading = false;
      }
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除这条对账记录吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          await reconciliationDelete(id);
          this.$message.success('删除成功');
          this.getList();
        } catch (error) {
          this.$message.error('删除失败');
          console.error(error);
        }
      }).catch(() => {
        this.$message.info('已取消删除');
      });
    },

    // 提交表单
    async handleSubmit() {
      this.$refs.formData.validate(async (valid) => {
        if (valid) {
          try {
            await reconciliationSave(this.formData);
            this.$message.success('保存成功');
            this.dialogVisible = false;
            this.getList();
          } catch (error) {
            this.$message.error('保存失败');
            console.error(error);
          }
        }
      });
    },

    // 格式化金额
    formatMoney(value) {
      if (!value) return '0.00';
      return Number(value).toLocaleString('zh-CN', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },

    // 获取状态文本
    getStatusText(status) {
      const statusMap = {
        0: '未对账',
        1: '已对账',
        2: '差异',
        3: '已确认'
      };
      return statusMap[status] || '未知';
    },

    // 获取状态标签类型
    getStatusType(status) {
      const typeMap = {
        0: 'default',
        1: 'success',
        2: 'warning',
        3: 'info'
      };
      return typeMap[status] || 'default';
    }
  }
};
</script>

<style scoped>
.reconciliation-container {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-form {
  margin-bottom: 20px;
}

.toolbar {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  flex-wrap: wrap;
}

.reconciliation-stats {
  margin-bottom: 20px;
}

.stat-card {
  background-color: #f5f7fa;
  padding: 15px;
  border-radius: 4px;
  text-align: center;
}

.stat-title {
  font-size: 14px;
  color: #606266;
  margin-bottom: 8px;
}

.stat-number {
  font-size: 24px;
  font-weight: bold;
  color: #303133;
  margin-bottom: 4px;
}

.stat-amount {
  font-size: 16px;
  color: #67c23a;
  font-weight: 500;
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>
