import request from '@/api/request';

/**
 * 获取对账列表
 * @param {Object} params 查询参数
 * @param {number} params.pageNum 页码
 * @param {number} params.pageSize 每页条数
 * @param {number} params.accountId 账户ID（可选）
 * @param {string} params.reconciliationDate 对账日期（可选）
 * @param {number} params.status 状态（可选）
 * @returns {Promise}
 */
export function reconciliationGetList(params) {
  return request({
    url: '/reconciliation/page',
    method: 'get',
    params
  });
}

/**
 * 根据ID获取对账信息
 * @param {number} id 对账ID
 * @returns {Promise}
 */
export function reconciliationGetById(id) {
  return request({
    url: `/reconciliation/${id}`,
    method: 'get'
  });
}

/**
 * 保存对账信息（新增或更新）
 * @param {Object} data 对账数据
 * @param {number} data.id 对账ID（更新时必需）
 * @param {number} data.accountId 账户ID
 * @param {string} data.reconciliationDate 对账日期
 * @param {number} data.startBalance 期初余额
 * @param {number} data.endBalance 期末余额
 * @param {number} data.status 状态
 * @returns {Promise}
 */
export function reconciliationSave(data) {
  // 确保只传递后端实体类需要的字段
  const entityData = {
    id: data.id,
    accountId: data.accountId,
    reconciliationDate: data.reconciliationDate,
    startBalance: data.startBalance,
    endBalance: data.endBalance,
    status: data.status
  };

  return request({
    url: data.id ? '/reconciliation' : '/reconciliation',
    method: data.id ? 'put' : 'post',
    data: entityData
  });
}

/**
 * 删除对账信息
 * @param {number} id 对账ID
 * @returns {Promise}
 */
export function reconciliationDelete(id) {
  return request({
    url: `/reconciliation/${id}`,
    method: 'delete'
  });
}

/**
 * 批量删除对账信息
 * @param {Array} ids 对账ID数组
 * @returns {Promise}
 */
export function reconciliationBatchDelete(ids) {
  return request({
    url: '/reconciliation/batchDelete',
    method: 'delete',
    data: ids
  });
}
